import { wp } from 'window';

const { Fragment } = wp.element;
const { PluginSidebar } = wp.editPost;
const { PluginMoreMenuItem } = wp.editPost.__experimental;
const { registerPlugin } = wp.plugins;

const Component = () => (
	<Fragment>
		<PluginMoreMenuItem
			name="menu-item-name"
			type="sidebar"
			target="sidebar-name"
		>
			My Sidebar
		</PluginMoreMenuItem>
		<PluginSidebar
			name="sidebar-name"
			title="My Sidebar"
		>
			Content of the sidebar
		</PluginSidebar>
	</Fragment>
);

registerPlugin( 'distributor', {
	render: Component,
} );



